import { Component, Element, h, Host, Prop } from '@stencil/core';
import { FnIcon } from '../../../utils/functional/functional';
import { SharedStyles } from '../../../utils/shared-styles';

@Component({
  tag: 'storm-accondion-item',
  styleUrl: 'storm-accondion-item.css',
  shadow: true,
})
export class StormAccondionItem {
  @Element() host: HTMLElement;
  @Prop({ mutable: true }) expanded: boolean = true;
  @Prop() expandIconAlign: 'right' | 'left' = 'right';
  @Prop({ mutable: true }) expandIcon: string;
  @Prop({ mutable: true }) collapseIcon: string;

  handleToggle(e: MouseEvent) {
    if (e) {
      e.preventDefault();
      e.stopPropagation();
    }
    this.expanded = !this.expanded;
  }

  componentWillLoad() {
    SharedStyles.applyShadowRootStyles(this.host);
    if (this.expandIcon == null) {
      this.expandIcon = this.expandIconAlign === 'right' ? 'add' : 'chevron_right';
    }
    if (this.collapseIcon == null) {
      this.collapseIcon = this.expandIconAlign === 'right' ? 'remove' : 'expand_more';
    }
  }

  render() {
    return (
      <Host>
        <div class="accordion-item">
          <h5 class="accordion-header">
            <button
              class={{
                'p-2': true,
                'accordion-button': true,
                'font-weight-bold': true,
                collapsed: !this.expanded,
                primary: true,
              }}
              type="button"
              onClick={e => this.handleToggle(e)}
            >
              <FnIcon
                class={{
                  'mb-1': true,
                  'mx-1': true,
                  'position-absolute': this.expandIconAlign === 'right',
                  'end-0': this.expandIconAlign === 'right',
                }}
              >
                {this.expanded ? this.collapseIcon : this.expandIcon}
              </FnIcon>
              <slot name="storm-accordion-header"></slot>
            </button>
          </h5>
          <div
            class={{
              'accordion-collapse': true,
              'border-top': true,
              collapse: !this.expanded,
            }}
          >
            <div class="accordion-body p-2">
              <slot name="storm-accordion-body"></slot>
            </div>
          </div>
        </div>
      </Host>
    );
  }
}
